.Screen{
  min-height: 100vh;
  max-width:100vw;
  background-color: #131a2c;
  color:#FFFFFF;
}

.row {
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  height: 80vh;
}


.leftCol {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  flex:3;
}

.rightCol{
  flex:2;
}

.ButtonBar{
  display: flex;
  flex-direction: row;
  align-items: right;
  justify-content: right;
  font-size: calc(10px + 2vmin);
}

.ButtonDiv{
  margin: 15px 25px;
}

.icon{
  height:50px;
  width: 50px;
}

.Name{
  font-size: calc(10px + 5vmin);  
  font-weight: bold;
  margin: 20px;
  text-align: left;
  width: 600px;
}

.Intro{
  font-size: calc(2px + 2vmin);
  color: #8fa8c6;
  width:600px;
  letter-spacing: 1px;
  font-weight: 600px;
  text-align: left;
  justify-content: center;
}

.Template{
  padding-top:2vh;
  padding-bottom:2vh;
}

.Title{
  font-weight:bold;
}

.CompDate{
  color: #C17C74;
  font-size: calc(2px + 1.5vmin);
}

.Points{
  padding:20px;
  font-size: calc(4px + 1.5vmin);
  width: 500px;
  align-items: center;
  justify-content: center;
  line-height: 2.8vh;
}

.SkillPoints{
  padding:20px;
  font-size: calc(4px + 1.5vmin);
  padding-left:2vw;
  padding-right:2vw;
  align-items: center;
  justify-content: center;
  line-height: 2.8vh;
}

.NavBar{
  text-align: left;
  width:500px;
  margin: 20px;
  font-size: calc(2px + 2vmin);
  color:#FFFFFF

}

.Scroll{
  width:700px;
  font-size: calc(2px + 2vmin);
  text-align: left;
  margin: 0px;
  padding:0px;
}

.ScrollItem{
  font-size: calc(2px + 2vmin);
  text-align: left;
}

.Nav:hover{
  color: #C17C74;
}

.SkillRow{
  display: flex;
  flex-direction: row;
}

.SkillTemp{
  justify-content: space-evenly;
}